);\n//     position: relative;\n//     inset: 10px;\n//     .surfacePlot {\n//       height: auto !important;\n//     }\n//   }\n//   .model {\n//     .title {\n//       bottom: 24px !important;\n//     }\n//     .click {\n//       top: auto !important;\n//       bottom: 56px !important;\n//       flex-direction: column;\n//       button {\n//         width: 160px;\n//         padding: 10px 16px !important;\n//       }\n//     }\n//   }\n// }\n.tpl-box {\n  height: calc(100vh - 250px);\n  overflow: auto;\n\n  &::-webkit-scrollbar {\n    width: 5px;\n    height: 3px;\n  }\n\n  &::-webkit-scrollbar-thumb {\n    border-radius: 10px;\n    background: rgba(204, 204, 204, 0.5);\n  }\n\n  &::-webkit-scrollbar-track {\n    border-radius: 0;\n  }\n}\n\n.tpl {\n  display: grid;\n  grid: auto / repeat(6, 1fr);\n  gap: 24px;\n  padding: 0 12px 16px 12px;\n  overflow: auto;\n\n  .empty {\n    height: calc(100vh - 300px);\n    display: flex;\n    align-items: center;\n    flex-direction: column;\n    justify-content: center;\n    gap: 24px;\n    font-size: 14px;\n    color: #999;\n    grid-area: auto / span 6;\n\n    img {\n      width: 250px;\n    }\n  }\n\n  .offshelf {\n    grid-area: auto / span 1;\n    background: #fff;\n    // background: red;\n    box-shadow: 3px 4px 6px 0px rgba(63, 110, 243, 0.1);\n    border-radius: 10px;\n    // overflow: hidden;\n    aspect-ratio: 7 / 4;\n    height: auto;\n    position: relative;\n    overflow: hidden;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    font-size: 12px;\n    flex-direction: column;\n    color: #999;\n  }\n\n  .detail {\n    padding: 16px 12px 12px;\n    display: flex;\n    flex-direction: column;\n    gap: 14px 0;\n\n    .icons {\n      width: 16px;\n      height: 16px;\n      border-radius: 50%;\n      background: #ffe7dc;\n      color: #ff814a;\n      text-align: center;\n      line-height: 16px;\n      margin-top: -2px;\n      margin-right: 4px;\n    }\n\n    .p {\n      display: flex;\n      justify-content: space-between;\n      align-items: center;\n      color: #333;\n      font-size: 12px;\n    }\n  }\n\n  &-child {\n    grid-area: auto / span 1;\n    // background: #f4f4f4;\n    background: #f7f7f7;\n    // background: red;\n    border: 1px solid #f5f5f5;\n    // box-shadow: 3px 4px 6px 0px rgba(63, 110, 243, 0.1);\n    border-radius: 10px 10px 0 0;\n    // overflow: hidden;\n    aspect-ratio: 6 / 4;\n    height: auto;\n    position: relative;\n\n    .version {\n      position: absolute;\n      width: max-content;\n      left: -4px;\n      bottom: 2px;\n      background: rgba(0, 0, 0, .4);\n      border-radius: 4px;\n      z-index: 999;\n      color: #fff;\n      font-size: 12px;\n      padding: 6px 12px;\n      transform: scale(.9);\n      font-weight: bold;\n    }\n\n    .icon {\n      position: absolute;\n      top: 8px;\n      right: 8px;\n      width: 24px;\n      height: 24px;\n      // background: #ececec;\n      border-radius: 4px;\n      z-index: 9;\n      padding-left: 1px;\n      box-sizing: border-box;\n      cursor: pointer;\n    }\n\n    .new {\n      right: 16px;\n      width: 24px;\n      border-radius: 0;\n      position: absolute;\n      top: 0;\n      z-index: 9;\n      animation: slidein 0.5s linear;\n      color: #fff;\n      box-sizing: border-box;\n    }\n\n    .header {\n      background: #dddddd;\n      border-radius: 8px 10px 0 0;\n      height: 20px;\n      position: absolute;\n      top: 0;\n      z-index: 9;\n      width: 100%;\n      animation: slidein 0.5s linear;\n      color: #fff;\n      padding-left: 12px;\n      line-height: 20px;\n      box-sizing: border-box;\n    }\n\n    .surfacePlot {\n      position: absolute;\n      inset: 12px;\n      width: calc(100% - 24px);\n      height: calc(100% - 24px);\n      object-fit: cover;\n      border-radius: 10px 10px 0 0;\n      animation: slidein 0.5s linear;\n    }\n\n    &:hover {\n\n      .model,\n      .icon {\n        display: flex;\n      }\n\n      .header,\n      .tags,\n      .new {\n        display: none;\n      }\n\n      .image-box {\n        width: 100% !important;\n        height: auto !important;\n        inset: 0 !important;\n      }\n\n      .surfacePlot {\n        inset: 0 !important;\n        width: 100%;\n        height: 100%;\n      }\n    }\n\n    .model {\n      display: none;\n      width: 100%;\n      // height: 100%;\n      position: absolute;\n      border-radius: 10px 10px 0 0;\n      animation: slidein 0.5s linear;\n      inset: 0;\n      background: rgb(0, 0, 0, 0.4);\n\n      // box-shadow: inset 0px 80px 80px -80px #666,\n      //   inset 0px -80px 80px -80px #666;\n      p.title {\n        display: block;\n        width: 100%;\n        box-sizing: border-box;\n        text-align: center;\n        position: absolute;\n        bottom: 16px;\n        font-size: 14px;\n        color: #fff;\n        padding: 0 16px;\n      }\n\n      .click {\n        display: flex;\n        flex-direction: column;\n        align-items: center;\n        justify-content: center;\n        width: 100%;\n        gap: 16px;\n        position: absolute;\n        // bottom: 16px;\n        inset: 0;\n\n        button {\n          margin-left: 0;\n          width: 160px;\n          padding: 6px 12px;\n          background: #6a99ff;\n          border-radius: 5px;\n          color: #fff;\n          font-size: 12px;\n          border: 0;\n          min-width: 80px;\n\n          &:hover {\n            opacity: 0.9;\n            cursor: pointer;\n          }\n        }\n      }\n    }\n\n    .tags {\n      position: absolute;\n      top: 8px;\n      left: 12px;\n      // width: 100%;\n      // display: none;\n      align-items: center;\n      justify-content: flex-end;\n      animation: slidein 0.5s linear;\n      gap: 12px;\n      z-index: 9;\n\n      p {\n        display: block;\n        padding: 6px 12px;\n        border-radius: 4px;\n        font-size: 12px;\n        color: #fff;\n      }\n    }\n  }\n}\n\n.box {\n  background: #ffffff;\n  box-shadow: 3px 4px 6px 0px rgba(63, 110, 243, 0.1);\n  border-radius: 8px;\n  padding: 25px 16px 0px 16px;\n  display: flex;\n  flex-direction: column;\n  gap: 16px;\n\n  .search {\n    display: flex;\n    align-items: center;\n    justify-content: flex-end;\n\n    .label {\n      font-size: 12px;\n      color: #555555;\n      margin-right: 8px;\n      margin-left: 16px;\n    }\n\n    .part {\n      width: 160px;\n    }\n\n    .part+.part {\n      margin-left: 16px;\n    }\n  }\n}\n"],"sourceRoot":""}]);
